const ourWorksBtn = document.getElementById("our-works");
const contactUsBtn = document.getElementById("contact-us");
const sectionWorks = document.querySelector(".section__works");
const sectionContact = document.querySelector(".section__contact");
const navLink = document.querySelectorAll(".nav__links--link");

// Button Scrolling
ourWorksBtn.addEventListener("click", function () {
  // 1.Scrooling
  // const sectionWorksCoords = sectionWorks.getBoundingClientRect();
  // window.scrollTo({
  //   left: sectionWorksCoords.left + window.pageXOffset,
  //   top: sectionWorksCoords.top + window.pageYOffset,
  //   behavior: "smooth",
  // });

  // 2.Scrooling  -Modern way-
  sectionWorks.scrollIntoView({ behavior: "smooth" });
});

contactUsBtn.addEventListener("click", function () {
  sectionContact.scrollIntoView({ behavior: "smooth" });
});

